


























export default {
	data() {
		return {
			phone:'',
			code:'',
			psw:'',
			repsw:''
		}
	},
	methods:{
		getCode(){
			uni.request({
				url:'http://bus.liebianzhe.com/api/login/sendCode',
				data:{
					phone:this.phone
				},
				success: (res) => {
					console.log(res)
				}
			})
		},
		order(){
			if(!/^(1[345678]\d{9}$)/.test(this.phone) || this.phone==''){
				uni.showToast({
					title:'请输入正确手机号'
				})
				return
			}
			if(this.psw=='' || this.repsw==''){
				uni.showToast({
					title:'密码不能为空'
				})
				return
			}
			if(this.psw!=this.repsw){
				uni.showToast({
					title:'密码不一致'
				})
				return
			}
			uni.request({
				url:'http://bus.liebianzhe.com/api/login/forget_pass',
				data:{
					phone:this.phone,
					code:this.code,
					password:this.psw,
					repassword:this.repsw
				},
				success: (res) => {
					if(res.data.code==1){
						console.log('chengg')
					}else{
						uni.showToast({
							title:res.data.msg
						})
					}
				}
			})
		}
	}
}
